import { createSlice } from "@reduxjs/toolkit";

export const shirtSlice = createSlice({
  name: "shirt",
  initialState: {
    currentShirtId: false,
    currentShirtTitle: "",
    currentShirtImage: "",
    currentShirtPrice: "",
    currentShirtQuantity: "",
    currentShirtColor: "black",
    cartOpen: false,
    selectedCategory: "tshirt",

    cart: [],
    cartTotal: 0,
    orderInfo: false,
    orderSuccess: false,
  },
  reducers: {
    setCurrentShirtId: (state, action) => {
      const {
        currentShirtId,
        currentShirtTitle,
        currentShirtImage,
        currentShirtPrice,
      } = action.payload;
      state.currentShirtId = currentShirtId;
      state.currentShirtTitle = currentShirtTitle;
      state.currentShirtImage = currentShirtImage;
      state.currentShirtPrice = currentShirtPrice;
    },
    toggleCart: (state, action) => {
      const clearCart = action.payload;
      state.cartOpen = !state.cartOpen;
      state.orderSuccess = false;

      if (clearCart) {
        state.cart = [];
      }
    },
    addToCart: (state, action) => {
      const { size, color } = action.payload;
      let index = false;
      if (
        !state.cart.some((e, i) => {
          const cond = e?.shirtId === state.currentShirtId;
          index = cond ? i : false;
          return cond;
        })
      ) {
        state.cart.push({
          shirtId: state.currentShirtId,
          title: state.currentShirtTitle,
          image: state.currentShirtImage,
          price: state.currentShirtPrice,
          quantity: state.currentShirtQuantity,
          color: color,
          size: size,
        });
      } else {
        if (index >= 0) {
          state.cart.splice(index, 1);
        }
      }
    },
    removeFromCart: (state, action) => {
      const shirtId = action.payload;
      let index = false;
      if (
        state.cart.some((e, i) => {
          const cond = e?.shirtId === shirtId;
          index = cond ? i : false;
          return cond;
        })
      ) {
        if (index >= 0) {
          state.cart.splice(index, 1);
        }
      }
    },

    selectCategory: (state, action) => {
      const category = action.payload;
      state.selectedCategory = category;
    },
    completeOrder: (state, action) => {
      const orderInfo = action.payload;
      state.orderSuccess = true;
      state.orderInfo = orderInfo;
    },
    setCartTotal: (state, action) => {
      const total = action.payload;
      state.cartTotal = total;
    },
  },
});

export const {
  setCurrentShirtId,
  addToCart,
  toggleCart,
  removeFromCart,
  selectCategory,
  setCartTotal,
  completeOrder,
} = shirtSlice.actions;

export default shirtSlice.reducer;
